import removeNonDigitCharacters from "./removeNonDigitCharacters";
/*
  token in -> primary: '$cyan9'
*/
export default (function (theme) {
  var output = {};
  Object.keys(theme).forEach(function (usage) {
    Object.keys(theme[usage]).forEach(function (colorName) {
      var key = "".concat(usage).concat(removeNonDigitCharacters(colorName));
      var color = theme[usage][colorName];
      output[key] = "$".concat(color.data.codeName);
    });
  });
  return output;
});